import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import Cookie from "@/utils/cookie";

Vue.use(Vuex);

export default new Vuex.Store({
	state:{
		loading:false,
		token:Cookie.get('cc_token')||'',
		breadcrumb:[],
		layout:false,
		selectedMenu:'',
		userName:Cookie.get('cc_user_name')||'',
		identity:Cookie.get('cc_identity')||''
	},
	//mutations,
	mutations,
	actions,
	modules:{
		//auth
	}
})


