<template>
	<div>
		<div v-if="!showDetail">
			<el-button @click="onAdd" style="margin-bottom:10px;float:right">新增月主题</el-button>
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      type="index"
			      label="月编号"
			      width="80">
			    </el-table-column>
			    <el-table-column
			      label="月主题"
			      width="180">
			      <template slot-scope="scope">
					<router-link :to="{name:'TextBookWeek', params:{bookId:bookId,monthId: scope.row.id }}">{{scope.row.title}}</router-link>
					<!-- <button @click="go2Detail(scope.row.id)">{{scope.row.address}}</button> -->
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="cover"
			      label="月封面">
			      <template slot-scope="scope">
			      	<img style="height:60px;width:auto;" v-if="scope.row.cover" :src="scope.row.cover" alt="">
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="class_count"
			      label="课时数">
			    </el-table-column>
			    <el-table-column
			      prop="child_count"
			      label="周数">
			    </el-table-column>
				<el-table-column
			      label="操作"
			      width="120">
			      <template slot-scope="scope">
			      	<el-button
			          @click="editRow(scope.row)"
			          type="text"
			          size="small">
			          编辑
			        </el-button>
			        <el-button
			          @click="deleteRow(scope.$index,scope.row)"
			          type="text"
			          size="small">
			          删除
			        </el-button>
			      </template>
			    </el-table-column>
			</el-table>
			<el-dialog :title="dialogTitle" :visible.sync="showDialog">
			  <el-form :model="textbook_month" label-width="70px">
			    <el-form-item label="月主题">
			      <el-input v-model="textbook_month.name" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="月封面">
			      <div>
			      	<div class="pic-box"><img v-if="textbook_month.cover" :src="textbook_month.cover" alt=""></div>
			      	<el-button @click="onShowCropper" class="up-btn">选择图片</el-button>
			      </div>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="showDialog = false">取 消</el-button>
			    <el-button type="primary" @click="onSave">保 存</el-button>
			  </div>
			</el-dialog>
			<cropper :ratio="4/3" v-if="imgDialog" :params="{uploadType:'textbook'}" :visible="imgDialog" @closeModal="closeCropper" @onUploaded="onUploaded"></cropper>
		</div>
		<router-view v-else></router-view>
	</div>
	
</template>
<script>
import cropper from '@/components/common/cropper.vue'
import Service from '@/service/index'
	export default{
		data(){
			return{
				showDetail:false,
				bookId:this.$route.params.bookId,
				tableData: [],
		        dialogTitle:'新增月主题',
		        showDialog:false,
		        textbook_month:{
		        	id:'',
		        	name:'',
		        	cover:'',
		        	number:''
		        },
		        imgDialog:false,

			}
		},
		watch: {
		    '$route' (to, from) {
		    	if(to.name=='TextBookMonth'){
		    		this.showDetail=false;
		    		this.requestData();

		    	}else{
		    		this.showDetail=true;
		    	}
		    }
		     
		},
		mounted(){
			if(this.$route.params.monthId){
				this.showDetail=true;
			}else{
				this.showDetail=false;
			}
			this.requestData();
		},
		methods:{
			requestData(){
				Service.textBookMonthList(this.bookId).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onSave(){
				let json={
					title:this.textbook_month.name,
					cover:this.textbook_month.cover,
					textbook_id:this.bookId,
					calendar_type:'month',
					number:this.tableData.length+1
				};
				if(this.textbook_month.id){
					json.id=this.textbook_month.id;
					json.number=this.textbook_month.number;
				}
				Service.saveTextBookCalendar(json).then(res=>{
					if(res.data.result=='success'){
						this.$message.success(res.data.message);
						this.showDialog=false;
						this.requestData();
					}else{
						this.$message.warning(res.data.message);
					}
				})
			},
			onAdd(){
				this.textbook_month={
					id:'',
		        	name:'',
		        	cover:'',
		        	number:''
				}
				this.dialogTitle="新增月主题";
				this.showDialog=true;
			},
			editRow(row){
				this.textbook_month={
					id:row.id,
		        	name:row.title,
		        	cover:row.cover,
		        	number:row.number
				}
				this.dialogTitle='编辑月主题';
				this.showDialog=true;
			},
			deleteRow(idx,row){
				this.$confirm('确认删除吗？', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		        	//删除接口
		        	Service.delTextBookCalendar(row.id).then(res=>{
						if(res.data.result=='success'){
							this.$message.success(res.data.message);
							this.showDialog=false;
							this.requestData();
							this.$message({
					            type: 'success',
					            message: '删除成功!'
					        });
						}else{
							this.$message.warning(res.data.message);
						}
					})
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		          this.tableData.splice(idx,1);
		        })
			},
			onShowCropper(){
		        this.imgDialog=true;
		      },
		      closeCropper(){
		        this.imgDialog=false;
		      },
		      onUploaded(url){
		      	this.textbook_month.cover=url;
		      	this.imgDialog=false;
		      },
		      go2Detail(id){
		      	// this.$router.push({ name: 'TextBookWeek', params: { bookId:this.$route.params.bookId,monthId:id }})
		      	this.$router.push(`/teachingmanage/textbook/${this.$route.params.bookId}/${id}`)
		      }

		},
		components:{
			cropper
		}
	}
</script>
<style scope>
	.pic-box{
		display: inline-block;
		width:200px;
		height:150px;
		border:1px solid #e0e0e0;
		overflow: hidden;
	}
	.pic-box img{
		width:100%;
	}
	.up-btn{
		vertical-align: text-bottom;
	    margin-left: 10px;
	    margin-bottom: 5px;
	}
</style>


