<template>
	<div>
		<el-form :inline="true" :model="search" class="demo-form-inline">
		  <!-- <el-form-item label="关键字">
		    <el-input v-model="search.key" placeholder="用户名或手机"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSearch">查询</el-button>
		  </el-form-item> -->
		  <el-form-item style="float:right">
		  	<el-button @click="onAdd" style="float:right">新增老师</el-button>
		  </el-form-item>
		</el-form>
		
		<el-table
		    :data="tableData"
		    border
		    style="width: 100%">
		    <el-table-column
		      prop="id"
		      label="用户ID"
		      width="80">
		    </el-table-column>
		    <el-table-column
		      prop="name"
		      label="名称">
		    </el-table-column>
		    <el-table-column 
		      prop="qr"  
		      label="二维码">
		      <template slot-scope="scope">
		      	<img style="height:60px;width:auto;" :src="scope.row.qr" alt="">
		      </template>
		    </el-table-column>
		    <el-table-column 
		      label="类型"
		      >
		      <template slot-scope="scope">
		      	{{getTypeLabel(scope.row.type)}}
		      </template>
		    </el-table-column>
			<el-table-column
		      label="操作">
		      <template slot-scope="scope">
		      	<el-button
		          @click="editRow(scope.row)"
		          type="text"
		          size="small">
		          编辑
		        </el-button>
		        <el-button
		          @click="deleteRow(scope.row.id)"
		          type="text"
		          size="small">
		          删除
		        </el-button>
		      </template>
		    </el-table-column>
		</el-table>
		<div class="pagelist">
			<el-pagination
			  background
			  layout="prev, pager, next,total"
			  @current-change="onPageChange"
			  :current-page.sync="page.current"
			  :page-size="page.pageSize"
			  :total="page.total">
			</el-pagination>
		</div>
	
		<el-dialog :title="dialogTitle" v-if="showDialog" :visible.sync="showDialog">
		  <el-form ref="saveuser" :rules="rules" :model="user" label-width="70px">
		    <el-form-item label="名称" prop="name">
		      <el-input v-model="user.name" auto-complete="off"></el-input>
		    </el-form-item>
		    <el-form-item label="二维码">
			   <div>
		      	<div class="pic-box"><img v-if="user.wechatUrl" :src="user.wechatUrl" alt=""></div>
		      	<el-button @click="onShowCropper" class="up-btn">选择图片</el-button>
		      </div>
		    </el-form-item>
		    <el-form-item label="类型" prop="type">
		       <el-checkbox-group v-model="user.type">
			    <el-checkbox label="supervise">督导老师</el-checkbox>
			    <el-checkbox label="lecturer">讲师</el-checkbox>
			  </el-checkbox-group>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="showDialog = false">取 消</el-button>
		    <el-button type="primary" @click="onSave">保 存</el-button>
		  </div>
		</el-dialog>
		<cropper :ratio="1/1" v-if="imgDialog" :params="{uploadType:'teacher'}" :visible="imgDialog" @closeModal="closeCropper" @onUploaded="onUploaded"></cropper>
	</div>
</template>
<script>
import Service from '@/service/index'
import cropper from '@/components/common/cropper.vue'
	export default{
		data(){
			return{
				search:{
					key:''
				},
				showDetail:false,
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        },
		        dialogTitle:'',
		        showDialog:false,
		        imgDialog:false,
		        user:{
		        	id:'',
		        	name:'',
		        	wechatUrl:'',
		        	type:[]
		        },
		         rules: {
		          name: [
		            {required: true, message:'不能为空' ,trigger: 'blur'},
		          ],
		          type:[
		          	{required: true, message:'至少选择一项' ,trigger: 'change'}
		          ]
		      	}

			}
		},
		
		mounted(){
			this.requestData();
		},
		methods:{
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize
	    		}
				Service.teacherList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			onSave(){
				 this.$refs['saveuser'].validate((valid) => {
				 	if(valid){
				 		let json={
							teacher_name:this.user.name,
							teacher_qr:this.user.wechatUrl,
							teacher_type:this.user.type.join(',')
						};
						if(this.user.id){
							json.teacher_id=this.user.id;
						}
						Service.saveTeacher(json).then(res=>{
							if(res.data.result=='success'){
								this.$message.success(res.data.message);
								this.showDialog=false;
								this.page.current=1;
								this.requestData();
							}else{
								this.$message.warning(res.data.message);
							}
						})
				 	}
				 	
				 });
				
			},
			onAdd(){
				this.user={
		        	id:'',
		        	name:'',
		        	wechatUrl:'',
		        	type:[]
		        },
				this.dialogTitle='新增用户';
				this.showDialog=true;
			},
			editRow(row){
				this.user={
					id:row.id,
		        	name:row.name,
		        	wechatUrl:row.qr,
		        	type:row.type.split(',')
				}
				this.dialogTitle='编辑用户';
				this.showDialog=true;
			},
			//删除
			deleteRow(id){
				this.$confirm('确认删除吗？', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		        	Service.deleteTeacher(id).then(res=>{
					if(res.data.result=="success"){
						 this.$message({
				            type: 'success',
				            message: '删除成功!'
				          });
						 this.page.current=1;
						this.requestData();
					}else{
						this.$message({
				            type: 'error',
				            message: res.data.message
				          });
					}
				})
		         
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消删除'
		          });          
		        });
			},
			//点击查询
			onSearch(){
				//调用查询接口
				this.requestData();
			},
			onShowCropper(){
		        this.imgDialog=true;
		    },
			closeCropper(){
				this.imgDialog=false;
			},
			onUploaded(url){
				this.user.wechatUrl=url;
				this.imgDialog=false;
			},
			getTypeLabel(label){
				let typeArr=label.split(','),
					typeLabelArr=[];
				typeArr.forEach(t=>{
					if(t=='supervise'){
						typeLabelArr.push('督导老师')
					}else if(t=='lecturer'){
						typeLabelArr.push('讲师')
					}
				});
				return typeLabelArr.join(',');
			}

		},
		components:{
			cropper
		}
	}
</script>
<style scoped>
	.pic-box{
		display: inline-block;
		width:200px;
		height:200px;
		border:1px solid #e0e0e0;
		overflow: hidden;
		vertical-align: bottom;
	}
	.pic-box img{
		width:100%;
	}
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


