import Vue from 'vue';

const _baseUrl=process.env.API_URL;
export default{
	login (json) {
	    let url = `${_baseUrl}/api/login`;
	    return Vue.http.post(url,{"login":json.username,"password":json.password})
	},
	//保存教材
	saveTextBook(json){
		let url = `${_baseUrl}/api/adm/textbook`;
	    return Vue.http.post(url,json)
	},
	//教材列表
	textBookList(json){
		let url = `${_baseUrl}/api/adm/textbook`;
	    return Vue.http.get(url,{params:json})
	},
    //班级管理列表
    classList(json){
        let url = `${_baseUrl}/api/class/list`;
        return Vue.http.get(url,{params:json})
    },
    //班级用户列表
    classUserList(json){
        let url = `${_baseUrl}/api/class/user/list`;
        return Vue.http.get(url,{params:json})
    },
	//删除教材
	delTextBook(bookId){
		let url = `${_baseUrl}/api/adm/textbook/${bookId}`;
	    return Vue.http.delete(url)
	},
	//保存材料日历
	saveTextBookCalendar(json){
		let url = `${_baseUrl}/api/adm/textbook/calendar`;
	    return Vue.http.post(url,json)
	},
	//教材月日历数据列表
	textBookMonthList(bookId){
		let url = `${_baseUrl}/api/adm/textbook/${bookId}/month`;
	    return Vue.http.get(url)
	},
	//删除教材日历
	delTextBookCalendar(id){
		let url = `${_baseUrl}/api/adm/textbook/calendar/${id}`;
	    return Vue.http.delete(url)
	},
	//教材周日历数据列表
	textBookWeekList(bookId,monthId){
		let url = `${_baseUrl}/api/adm/textbook/${bookId}/month/${monthId}/week`;
	    return Vue.http.get(url)
	},
	//教材天日历数据列表
	textBookDayList(bookId,weekId){
		let url = `${_baseUrl}/api/adm/textbook/${bookId}/week/${weekId}/day`;
	    return Vue.http.get(url)
	},
	//保存课时
	saveTextBookClass(json){
		let url = `${_baseUrl}/api/adm/textbook/class`;
	    return Vue.http.post(url,json)
	},
	//课时列表
	textBookClassList(dayId){
		let url = `${_baseUrl}/api/adm/textbook/day/${dayId}/class`;
	    return Vue.http.get(url)
	},
	//课时详情
	textBookClassDetail(classId){
		let url = `${_baseUrl}/api/adm/textbook/class/${classId}`;
	    return Vue.http.get(url)
	},
	//删除教材课时
	delTextBookClass(id){
		let url = `${_baseUrl}/api/adm/textbook/class/${id}`;
	    return Vue.http.delete(url)
	},
	//根据教材ID获取课时列表
	getClassesByTextBookId(textboodId){
		let url = `${_baseUrl}/api/adm/textbook/${textboodId}/dates`;
	    return Vue.http.get(url)
	},
	//保存课程
	saveCourse(json){
		let url = `${_baseUrl}/api/adm/course`;
	    return Vue.http.post(url,json);
	},
	//课程列表
	courseList(json){
		let url = `${_baseUrl}/api/adm/courses`;
	    return Vue.http.get(url,{params:json});
	},
	//课程详情
	courseDetail(courseId){
		let url = `${_baseUrl}/api/adm/course/${courseId}`;
	    return Vue.http.get(url);
	},
	//用户列表
	userList(json){
		let url = `${_baseUrl}/api/adm/users`;
	    return Vue.http.get(url,{params:json});
	},
	//用户列表
	orderList(json){
		let url = `${_baseUrl}/api/adm/orders`;
	    return Vue.http.get(url,{params:json});
	},
	//保存用户
	saveUser(json){
		let url = `${_baseUrl}/api/adm/user`;
	    return Vue.http.post(url,json);
	},
	//课程用户列表
	courseUserList(json){
		let url = `${_baseUrl}/api/adm/course/${json.course_id}/users`;
	    return Vue.http.get(url,{params:json});
	},
	//课程添加用户
	courseAddUser(json){
		let url = `${_baseUrl}/api/adm/course/${json.courseId}/user`;
	    return Vue.http.post(url,{user_ids:json.userIds});
	},
	//从课程中移除用户
	courseDeleteUser(courseId,userId){
		let url = `${_baseUrl}/api/adm/course/${courseId}/user`;
	    return Vue.http.delete(url,{params:{user_id:userId}});
	},
	//老师列表
	teacherList(json){
		let url = `${_baseUrl}/api/teacher/list`;
	    return Vue.http.get(url,{params:json});
	},
	//保存老师
	saveTeacher(json){
		let url = `${_baseUrl}/api/teacher/save`;
	    return Vue.http.post(url,json);
	},
	//删除老师
	deleteTeacher(id){
		let url = `${_baseUrl}/api/teacher/delete`;
	    return Vue.http.delete(url,{params:{teacher_id:id}});
	},
	//保存课程班级
	saveCourseClass(json){
		let url = `${_baseUrl}/api/class/save`;
	    return Vue.http.post(url,json);
	},
	//删除课程班级
	deleteCourseClass(id){
		let url = `${_baseUrl}/api/class/delete`;
	    return Vue.http.delete(url,{params:{class_id:id}});
	},
	//删除课程班级中的用户
    deleteClassUser(id){
    let url = `${_baseUrl}/api/class/user/delete`;
    return Vue.http.delete(url,{params:{id:id}});
},
	//导入用户
	importExcelToUser(data){
		let url = `${_baseUrl}/api/course/upload/user`;
        return Vue.http.post(url,data);
	},
	//删除用户
    deleteUsers(id){
        let url = `${_baseUrl}/api/user/delete`;
        return Vue.http.delete(url,{params:{user_id:id}});
	},
	//更新班级中的用户
    saveClassUser(json){
        let url = `${_baseUrl}/api/class/user/save`;
        return Vue.http.post(url,json);
    },
}


