<template>
	<div>
		<div v-show="!showDialog">
			<el-button @click="onAdd" style="margin-bottom:10px;float:right">新增课时</el-button>
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      type="index"
			      label="课时编号"
			      width="150">
			    </el-table-column>
			    <el-table-column
			      prop="title"
			      label="课时名称"
			      >
			    </el-table-column>
				<el-table-column
			      label="操作"
			      >
			      <template slot-scope="scope">
			      	<el-button
			          @click="editRow(scope.row)"
			          type="text"
			          size="small">
			          编辑
			        </el-button>
			        <el-button
			          @click="deleteRow(scope.$index,scope.row)"
			          type="text"
			          size="small">
			          删除
			        </el-button>
			      </template>
			    </el-table-column>
			</el-table>
		</div>
		<div style="margin-right:10px;" v-show="showDialog">
			<p class="edit_area_title">{{dialogTitle}}</p>
			<el-form :model="textbook_day" label-width="70px">
			    <el-form-item label="课时名称">
			      <el-input v-model="textbook_day.name" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="按年龄">
			      <el-select v-model="textbook_day.byage" placeholder="请选择">
				    <el-option
				      v-for="item in classByAgeList"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
			    </el-form-item>
			    <el-form-item label="按等级">
			    	<el-select v-model="textbook_day.bylevel" placeholder="请选择">
					    <el-option
					      v-for="item in classByLevelList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
			    </el-form-item>
			    <el-form-item label="打卡方式">
			       <el-radio-group v-model="textbook_day.sign_style">
				    <el-radio label="score">笑脸</el-radio>
				    <el-radio label="picture">单词卡</el-radio>
				    <el-radio label="video">视频</el-radio>
				    <el-radio label="audio">跟唱</el-radio>
				  </el-radio-group>
			    </el-form-item>
			    <el-form-item label="内容">
			      <UE :defaultMsg="textbook_day.content" :config="config" id="ue_internaTest" ref="ue"></UE>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="footer_btns">
			    <el-button @click="showDialog = false">取 消</el-button>
			    <el-button type="primary" @click="onSave">保 存</el-button>
			  </div>
		</div>
	</div>
	

	
</template>
<script>
import UE from '@/components/common/UE.vue'
import Service from '@/service/index'
	export default{
		data(){
			return{
				bookId:this.$route.params.bookId,
				monthId:this.$route.params.monthId,
				weekId:this.$route.params.weekId,
				dayId:this.$route.params.dayId,
				tableData: [],
		        dialogTitle:'新增课时',
		        showDialog:false,
		        textbook_day:{
		        	id:'',
		        	name:'',
		        	cover:'',
		        	sign_style:'score',
		        	content:'',
		        	number:'',
		        	bylevel:'',
		        	byage:''
		        },
		        dMg:'',
		       	config:{
		          initialFrameWidth: null,
		          initialFrameHeight: 550,
		          // toolbars:[],
		          readonly:false
		        },
		        classByAgeList:[{value:0,label:'全部'},{value:1,label:'3岁以下'},{value:2,label:'3岁以上'}],
		        classByLevelList:[
		        	{value:0,label:'全部'},
		        	{value:1,label:'Level 1-Level 2'},
		        	{value:3,label:'Level 3-Level 4'},
		        	{value:5,label:'Level 5-Level 6'},
		        	{value:7,label:'Level 7-Level 10'}
		        ]

			}
		},
		
		mounted(){
			this.requestData();
		},
		methods:{
			requestData(){
				Service.textBookClassList(this.dayId).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onSave(){
				this.textbook_day.content=this.$refs.ue.getUEContent();
				let json={
					textbook_id:this.bookId,
					calendar_id:this.dayId,
					title:this.textbook_day.name,
					cover:this.textbook_day.cover,
					number:this.tableData.length+1,
					content:this.textbook_day.content,
					signin_type:this.textbook_day.sign_style,
					age:this.textbook_day.byage,
					level:this.textbook_day.bylevel,
				};
				if(this.textbook_day.id){
					json.id=this.textbook_day.id;
					json.number=this.textbook_day.number
				}
				Service.saveTextBookClass(json).then(res=>{
					if(res.data.result=='success'){
						this.$message.success(res.data.message);
						this.showDialog=false;
						this.requestData();
					}else{
						this.$message.warning(res.data.message);
					}
				})
			},
			onAdd(){
				this.textbook_day={
		        	id:'',
		        	name:'',
		        	cover:'',
		        	sign_style:'score',
		        	content:'',
		        	bylevel:'',
		        	byage:''
		        }
		        this.dialogTitle="新增课时";
				this.showDialog=true;
			},
			editRow(row){
				Service.textBookClassDetail(row.id).then(res=>{
					if(res.data.result=="success"){
						this.textbook_day={
							id:res.data.data.id,
				        	name:res.data.data.title,
				        	sign_style:res.data.data.signin_type,
				        	content:res.data.data.content,
				        	number:res.data.data.number,
				        	byage:res.data.data.age,
				        	bylevel:res.data.data.level
						}
						this.$refs.ue.setUEContent(res.data.data.content);
						this.dialogTitle='编辑课时';
						this.showDialog=true;
					}
				})
				
			},
			deleteRow(idx,row){
				this.$confirm('确认删除吗？', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		        	//删除接口
		        	Service.delTextBookClass(row.id).then(res=>{
						if(res.data.result=='success'){
							this.$message.success(res.data.message);
							this.showDialog=false;
							this.requestData();
							this.$message({
					            type: 'success',
					            message: '删除成功!'
					        });
						}else{
							this.$message.warning(res.data.message);
						}
					})
		        })
			},

		},
		components:{
			UE
		}
	}
</script>
<style scope>
	.edit_area_title{
		font-size:18px;
		padding:10px 0;
		text-align: center;
	}
	.footer_btns{
		text-align: center;
	}
</style>


