<template>
	<div>
		<div v-if="!showDetail">
            <div style="margin-bottom:10px;float:right">
                <el-button @click="onAdd">新增班级</el-button>
                <input style="display: none;" ref="myinput" type="file" accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" @change="getFile">
                <el-button @click="exportData">导出</el-button>
                <el-button @click="insertData">导入</el-button>
            </div>

			<el-table
					:data="tableData"
					border
					style="width: 100%">
				<el-table-column
						prop="class_name"
						label="班级名称"
				>
					<template slot-scope="scope">
						<router-link :to="{name:'ClassUserList', params: { classId: scope.row.id }}">{{scope.row.class_name}}</router-link>
					</template>
				</el-table-column>
                <el-table-column
                        prop="class_code"
                        label="班级编号"
                >
                </el-table-column>
				<el-table-column
					prop="teacher_name"
					label="督导老师"
				>
				</el-table-column>
				<el-table-column
					label="督导老师二维码"
				>
                <template slot-scope="scope">
                    <img style="height:60px;width:auto;" :src="scope.row.teacher_qr" alt="">
                </template>
				</el-table-column>
				<el-table-column
						prop="join_num"
						label="班级学生数"
				>
				</el-table-column>
                <el-table-column
                        prop="in_course_rate"
                        label="班级到课率(%)"
                >
                </el-table-column>
                <el-table-column
                        prop="view_rate"
                        label="班级浏览率(%)"
                >
                </el-table-column>
                <el-table-column
                        prop="complete_rate"
                        label="班级完课率(%)"
                >
                </el-table-column>
                <el-table-column
                        prop="trans_rate"
                        label="班级转化率(%)"
                >
                </el-table-column>
                <el-table-column
                        prop="repeat_pay_rate"
                        label="班级复购率(%)"
                >
                </el-table-column>
                <el-table-column
                  label="操作"
                  width="120">
                  <template slot-scope="scope">
                    <el-button
                      @click="editRow(scope.row)"
                      type="text"
                      size="small">
                      编辑
                    </el-button>
                    <el-button
                      @click="deleteRow(scope.row)"
                      type="text"
                      size="small">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
			</el-table>
			<div class="pagelist">
				<el-pagination
						background
						layout="prev, pager, next,total"
						@current-change="onPageChange"
						:current-page.sync="page.current"
						:page-size="page.pageSize"
						:total="page.total">
				</el-pagination>
			</div>
            <el-dialog :title="dialogTitle" v-if="showDialog" :visible.sync="showDialog">
              <el-form ref="save" :rules="rules" :model="classes" label-width="80px">
                <el-form-item label="班级名称" prop="name">
                  <el-input v-model="classes.name" auto-complete="off"></el-input>
                </el-form-item>
                  <el-form-item label="班级编号" prop="class_code">
                      <el-input v-model="classes.class_code" auto-complete="off"></el-input>
                  </el-form-item>
                <el-form-item label="督导老师" prop="teacher_id">
                 <el-select v-model="classes.teacher_id">
                     <el-option v-for="t in teacherArr" :key="t.id" :label="t.name" :value="t.id"></el-option>
                 </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="showDialog = false">取 消</el-button>
                <el-button type="primary" @click="onSave">保 存</el-button>
              </div>
            </el-dialog>
		</div>
		<router-view v-else></router-view>
	</div>
</template>
<script>
    import Service from '@/service/index'
    export default{
        data(){
            return{
                teacherArr:[],
                showDetail:false,
                course_id:this.$route.params.courseId,
                tableData: [],
                page:{
                    current:1,
                    pageSize:20,
                    total:0
                },
                dialogTitle:'',
                showDialog:false,
                classes:{
                    id:'',
                    name:'',
                    teacher_id:'',
                    class_code:''
                },
                rules: {
                  name: [
                    {required: true, message:'不能为空' ,trigger: 'blur'},
                  ],
                  teacher_id:[
                    {required: true, message:'请选择一项' ,trigger: 'change'}
                  ],
                    class_code:[
                        {required: true, message:'不能为空' ,trigger: 'blur'}
                    ]
                }
            }
        },
        watch: {
            '$route' (to, from) {
                if(to.name=='ClassList'){
                    this.showDetail=false;
                    this.requestData();
                    this.requestTeacherArr();
                }else{
                    this.showDetail=true;
                }
            }
        },
        mounted(){
            if(this.$route.params.classId){
                this.showDetail=true;
            }else{
                this.showDetail=false;
            }
            this.requestData();
            this.requestTeacherArr();
        },
        methods:{
            //导入
            insertData(){
                this.$refs.myinput.click();
            },
            //导出
            exportData(){
                let token=this.$store.state.token;
                if(token && token.split(' ').length>1){
                    token=token.split(' ')[1];
                }

                window.open(`${process.env.API_URL}/api/class/user/list?course_id=${this.course_id}&type=export&token=${token}`)
            },
            getFile(e){
                const data = new FormData();
                data.append('file',e.target.files[0])
                data.append('course_id',this.course_id)

                Service.importExcelToUser(data).then(res=>{
                    if(res.data.result=='success'){
                        this.page.current=1;
                        this.requestData();
                        this.$message.success('导入成功!');
                    }else{
                        this.page.current=1;
                        this.requestData();
                        let template = `<strong>${res.data.message}</strong>`;
                        res.data.data.forEach(v=>{
                            console.log(v)
                            let temp = `<p>用户：${v.mobile}</p><p>错误信息：${v.err}</p>`
                            template+=temp;
                        });
                        this.$notify({
                            title: '导入结果',
                            type: 'warning',
                            duration: 0,
                            dangerouslyUseHTMLString: true,
                            message: template
                        });
                        // this.$message.error(res.data.message);
                    }
                })

            },
            requestData(){
                let data={
                    page:this.page.current,
                    per_page:this.page.pageSize,
					course_id:this.course_id,
                }
                Service.classList(data).then(res=>{
                    if(res.data.result=='success'){

                    this.tableData=res.data.data.data;
                    this.page.current=res.data.data.current_page;
                    this.page.total=res.data.data.total;
                }else{
                    this.$message.error(res.data.message);
                }
            })
            },
            //获取所有供选择的督导老师
            requestTeacherArr(){
                let json={
                    data_type:'all',
                    teacher_type:'supervise'
                }
                Service.teacherList(json).then(res=>{
                    if(res.data.result=='success'){
                        this.teacherArr=res.data.data;
                    }else{
                        this.$message.error(res.data.message);
                    }
                })
            },
            onPageChange(current){
                this.page.current=current;
                this.requestData();
            },

            //点击查询
            onSearch(){
                //调用查询接口
                this.requestData();
            },
            onAdd(){
                this.classes={
                    id:'',
                    name:'',
                    teacher_id:'',
                    class_code:''
                },
                this.dialogTitle='新增班级';
                this.showDialog=true;
            },
            editRow(row){
                this.classes={
                    id:row.id,
                    name:row.class_name,
                    class_code:row.class_code,
                    teacher_id:row.teacher_id
                }
                this.dialogTitle='编辑用户';
                this.showDialog=true;
            },
            //删除
            deleteRow(id){
                this.$confirm('确认删除吗？', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    Service.deleteCourseClass(id).then(res=>{
                    if(res.data.result=="success"){
                         this.$message({
                            type: 'success',
                            message: '删除成功!'
                          });
                         this.page.current=1;
                        this.requestData();
                    }else{
                        this.$message({
                            type: 'error',
                            message: res.data.message
                          });
                    }
                })
                 
                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: '已取消删除'
                  });          
                });
            },
            onSave(){
                 this.$refs['save'].validate((valid) => {
                    if(valid){
                        let json={
                           class_name:this.classes.name,
                           course_id:this.course_id,
                            class_code:this.classes.class_code,
                           teacher_id:this.classes.teacher_id
                        };
                        if(this.classes.id){
                            json.class_id=this.classes.id;
                        }
                        Service.saveCourseClass(json).then(res=>{
                            if(res.data.result=='success'){
                                this.$message.success(res.data.message);
                                this.showDialog=false;
                                this.page.current=1;
                                this.requestData();
                            }else{
                                this.$message.warning(res.data.message);
                            }
                        })
                    }
                    
                 });
                
            },
        }
    }
</script>
<style scope>

	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


