<template>
  <div style="line-height: initial;">
    <!-- <script id="editor" type="text/plain"></script> -->
    <script :id="id" type="text/plain"></script>
  </div>
</template>
<script>
// import '@/assets/ueditor/ueditor.config.js'
// import '@/assets/ueditor/ueditor.all.min.js'
// import '@/assets/ueditor/lang/zh-cn/zh-cn.js'
// import '@/assets/ueditor/ueditor.parse.min.js'

import '~/ueditor/ueditor.config.js'
import '~/ueditor/ueditor.all.js'
import '~/ueditor/lang/zh-cn/zh-cn.js'
// import '~/ueditor/ueditor.parse.min.js'
  export default {
    name: 'UE',
    data () {
      return {
        editor: null,
        // id:"ue_"+(new Date).getTime()
      }
    },
    props: {
      defaultMsg: {
        type: String
      },
      config: {
        type: Object
      },
      id:{
        type:String
      }
    },
    watch:{
      defaultMsg(val, oldVal) {
        this.editor = UE.getEditor(this.id, this.config);
        if (val !== null) {
          this.editor.setContent(val);
        }
      },
      config:{
        handler(c,oldC){
          this.editor = UE.getEditor(this.id, c);
          this.editor.addListener("ready", function () {
            _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
          });
        },
        deep:true
      }
      
    },
    mounted() {
      const _this = this;
      this.editor = UE.getEditor(this.id, this.config); // 初始化UE
      this.editor.addListener("ready", function () {
        _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
      });
    },
    methods: {
      getUEContent() { // 获取内容方法
        return this.editor.getContent()
      },
      setUEContent(content){
        return this.editor.setContent(content)
      }
    },
    destroyed() {
      this.editor.destroy();
    }
  }
</script>


