<template>
	<div>
		<el-form :inline="true" :model="search" class="demo-form-inline">
		  <el-form-item label="关键字">
		    <el-input v-model="search.key" placeholder="用户名/手机/课程名称"></el-input>
		  </el-form-item>	  
		  <el-form-item>
		    <el-button type="primary" @click="onSearch">查询</el-button>
		  </el-form-item>
		</el-form>
		
		<el-table
		    :data="tableData"
		    border
		    style="width: 100%">
		    <el-table-column
		      prop="from_order_id"
		      label="订单号"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="course_title"
		      label="课程名称"
		      >
		    </el-table-column>
		    <el-table-column 
		      prop="mobile"  
		      label="客户手机号"
		      >
		    </el-table-column>
		    <el-table-column 
		      prop="username"  
		      label="客户昵称"
		      >
		    </el-table-column>
		    <el-table-column 
		      prop="from_key"  
		      label="openID"
		      >
		    </el-table-column>
		    <el-table-column 
		      prop="price"
		      label="付款金额（¥）"
		      >
		    </el-table-column>
		    <el-table-column 
		    	prop="pay_time"
		      label="购买时间"
		      >
		    </el-table-column>
		    <el-table-column
		      label="付款状态">
		      <template slot-scope="scope">
		      	{{scope.row.status|payMentFilter}}
		      </template>
		    </el-table-column>
			
		</el-table>
		<div class="pagelist">
			<el-pagination
			  background
			  layout="prev, pager, next,total"
			  @current-change="onPageChange"
			  :current-page.sync="page.current"
			  :page-size="page.pageSize"
			  :total="page.total">
			</el-pagination>
		</div>
	</div>
</template>
<script>
import Service from '@/service/index'
	export default{
		data(){
			return{
				search:{
					key:''
				},
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        }
			}
		},
		
		mounted(){
			
			this.requestData();
		},
		filters:{
			payMentFilter(val){
				return val=='1'?'已付款':'未付款'
			}
		},
		methods:{
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize,
	    			key:this.search.key
	    		}
				Service.orderList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			
			//点击查询
			onSearch(){
				//调用查询接口
				this.requestData();
			}

		}
	}
</script>
<style scope>
	
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


