<template>
	<div>
		<div v-if="!showDetail">
			<el-button @click="onAdd" style="margin-bottom:10px;float:right">新增教材</el-button>
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      type="index"
			      label="编号"
			      width="80">
			    </el-table-column>
			    <el-table-column
			      label="教材名称"
			      width="180">
			      <template slot-scope="scope">
					<router-link :to="{name:'TextBookMonth', params: { bookId: scope.row.id }}">{{scope.row.title}}</router-link>
			      </template>
			    </el-table-column>
			    <el-table-column
			     
			      label="适应年龄"
			      width="180">
			      <template slot-scope="scope">
			      	{{scope.row.adapt_age_min}}-{{scope.row.adapter_age_max}}
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="class_count"
			      label="课时数">
			    </el-table-column>
			    <el-table-column
			      prop="month_count"
			      label="月度数">
			    </el-table-column>
				<el-table-column
			      label="操作"
			      width="120">
			      <template slot-scope="scope">
			      	<el-button
			          @click="editRow(scope.row)"
			          type="text"
			          size="small">
			          编辑
			        </el-button>
			        <el-button
			          @click="deleteRow(scope.$index,scope.row)"
			          type="text"
			          size="small">
			          删除
			        </el-button>
			      </template>
			    </el-table-column>
			</el-table>
			<div class="pagelist">
				<el-pagination
				  background
				  layout="prev, pager, next,total"
				  @current-change="onPageChange"
				  :current-page.sync="page.current"
				  :page-size="page.pageSize"
				  :total="page.total">
				</el-pagination>
			</div>
			
			<el-dialog :title="dialogTitle" :visible.sync="showDialog">
			  <el-form :model="textbook" label-width="70px">
			    <el-form-item label="教材名称">
			      <el-input v-model="textbook.name" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="最小年龄">
			      <el-input v-model="textbook.minAge" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="最大年龄">
			      <el-input v-model="textbook.maxAge" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="教材封面">
			      <div>
			      	<div class="pic-box"><img v-if="textbook.cover" :src="textbook.cover" alt=""></div>
			      	<el-button @click="onShowCropper" class="up-btn">选择图片</el-button>
			      </div>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="showDialog = false">取 消</el-button>
			    <el-button type="primary" @click="onSave">保 存</el-button>
			  </div>
			</el-dialog>
			<cropper :ratio="4/3" v-if="imgDialog" :params="{uploadType:'textbook'}" :visible="imgDialog" @closeModal="closeCropper" @onUploaded="onUploaded"></cropper>
		</div>
		<router-view v-else></router-view>
	</div>
</template>
<script>
import cropper from '@/components/common/cropper.vue'
import Service from '@/service/index'
	export default{
		data(){
			return{
				showDetail:false,
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        },
		        dialogTitle:'',
		        showDialog:false,
		        textbook:{
		        	id:'',
		        	name:'',
		        	minAge:'',
		        	maxAge:'',
		        	cover:''
		        },
		        imgDialog:false,

			}
		},
		watch: {
		    '$route' (to, from) {
		    	if(to.name=='TextBook'){
		    		this.showDetail=false;
		    		this.requestData();
		    	}else{
		    		this.showDetail=true;
		    	}
		     }
		},
		mounted(){
			if(this.$route.params.bookId){
				this.showDetail=true;
			}else{
				this.showDetail=false;
			}
			this.requestData();
		},
		methods:{
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize
	    		}
				Service.textBookList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			onSave(){
				let json={
					title:this.textbook.name,
					cover:this.textbook.cover,
					adapt_age_min:this.textbook.minAge,
					adapter_age_max:this.textbook.maxAge
				};
				if(this.textbook.id){
					json.id=this.textbook.id;
				}
				Service.saveTextBook(json).then(res=>{
					if(res.data.result=='success'){
						this.$message.success(res.data.message);
						this.showDialog=false;
						this.page.current=1;
						this.requestData();
					}else{
						this.$message.warning(res.data.message);
					}
				})
			},
			onAdd(){
				this.textbook={
					id:'',
		        	name:'',
		        	adaptAge:'',
		        	cover:''
				}
				this.dialogTitle="新增教材";
				this.showDialog=true;
			},
			editRow(row){
				this.textbook={
					id:row.id,
		        	name:row.title,
		        	minAge:row.adapt_age_min,
		        	maxAge:row.adapter_age_max,
		        	cover:row.cover
				}
				this.dialogTitle='编辑教材';
				this.showDialog=true;
			},
			deleteRow(idx,row){
				this.$confirm('确认删除吗？', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
			        Service.delTextBook(row.id).then(res=>{
						if(res.data.result=='success'){
							this.$message.success(res.data.message);
							this.showDialog=false;
							this.page.current=1;
							this.requestData();
							this.$message({
					            type: 'success',
					            message: '删除成功!'
					        });
						}else{
							this.$message.warning(res.data.message);
						}
					})

		          
		        })
			},
			onShowCropper(){
		        this.imgDialog=true;
		      },
		      closeCropper(){
		        this.imgDialog=false;
		      },
		      onUploaded(url){
		      	this.textbook.cover=url;
		      	this.imgDialog=false;
		      },
		      go2Detail(id){
		      	this.$router.push({ name: 'TextBookMonth',meta:{title:'yyue'},params: { id: id }})
		      }

		},
		components:{
			cropper
		}
	}
</script>
<style scope>
	.pic-box{
		display: inline-block;
		width:200px;
		height:150px;
		border:1px solid #e0e0e0;
		overflow: hidden;
	}
	.pic-box img{
		width:100%;
	}
	.up-btn{
		vertical-align: text-bottom;
	    margin-left: 10px;
	    margin-bottom: 5px;
	}
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


