<template>
  <div class="wrap">
    <template v-if="layout===false">
       <router-view></router-view>
    </template>
    <template v-else>
      <div class="top">
        <figure class="logo"><img src="./assets/logos.png" alt=""></figure>
        <ul class="top-right">
          <li><figure><img src="./assets/user.jpg" alt=""></figure></li>
          <li>{{userName}}</li>
          <li><el-button @click="logout" type="text">退出</el-button></li>
        </ul>
      </div>
      <div class="container">
        <div class="left-box">
          <el-menu
          :unique-opened="true"
          :router="true"
          :defaultActive="selectedMenu"
            class="el-menu-vertical-demo"
           >
            <el-submenu index="1">
              <template slot="title">
                <span>教研管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/teachingmanage/textbook">教材管理</el-menu-item>
                <el-menu-item index="/teachingmanage/course">课程管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu> 
            <el-submenu index="2">
              <template slot="title">
                <span>用户管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/usermanage/list">用户列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu> 
            <el-submenu index="3">
              <template slot="title">
                <span>订单管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/ordermanage/list">订单列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">
                <span>教师管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/teachermanage/list">教师列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu> 
            
           <!--  <el-menu-item index="">教研管理</el-menu-item> -->
          </el-menu>

        </div>
        <div class="right-box">
          <div class="breadcrum-nav">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item v-for="b in breadcrumbs" :to="{ path: b.path }">{{b.name}}</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="page-box">
            <router-view></router-view>
          </div>
          
          
        </div>
      </div>
    </template>
  </div>
</template>
<script>
  //import AppHeader from '@/components/common/NavHeader.vue';
  //import AppLeftMenu from '@/components/common/LeftMenu.vue';
  // import defaultLogo from '../static/img/logo_60kou.png'
  export default {
    data(){
      return {
        
      }
    },
   
    computed: {
      layout(){
        return this.$store.state.layout
      },
      breadcrumbs(){
        return this.$store.state.breadcrumb
      },
      selectedMenu(){
        let menu = this.$store.state.selectedMenu;
        let p = menu.split('/');
        let module = p[1];
        let page = p[2];
        if(page){
          return '/'+module+'/'+page;
        }else{
          return this.$store.state.selectedMenu;
        }
        
      },
      userName(){
        return this.$store.state.userName
      }
    },
    methods:{
      logout(){
        this.$confirm("确认要退出吗？","提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('logout')
          this.$router.push('/login')
        })
      }
    },
    components: {
      //AppHeader,
      //AppLeftMenu
    }
  }
</script>
<style>
html,body,div,ul,li,figure{padding:0;margin:0;box-sizing: border-box;}
  html,body{
    height:100%;
    font-size:14px;
    color:#393939;
    font-family: 'Open Sans'
  }
  .el-menu{
    border-right:none;
  }
  .wrap{
    position: relative;
    height:100%;
    width:100%;
    overflow:hidden;
  }
  .top{
    position: fixed;
    top:0;
    left:0;
    width:100%;
    height:70px;
    line-height: 70px;
    border-bottom:1px solid #d5d5d5;
  }
  .top .logo{
    float:left;
    margin-top:5px;
    width:145px;
    height:60px;
  }
  .top .logo img{
    height:100%;
  }
  .top-right{
    float:right;
    height:100%;
    list-style: none;
  }
  .top-right li{
    float:left;
    margin-right: 15px;
  }
  .top-right figure{
    width:43px;
    height:43px;
    border-radius: 50%;
    margin-top:12px;
  }

  .container{
    position: absolute;
    left:0;
    right:0;
    top:70px;
    bottom:0;
    overflow: hidden;
  }
  .container .left-box{
    position: absolute;
    width:190px;
    top:0;
    bottom:0;
    left: 0;
    overflow-y: auto;
    border-right:1px solid #d5d5d5;
  }
  .container .right-box{
    position: absolute;
    left:190px;
    right:0;
    top:0;
    bottom:0;
    overflow-y: auto;
  }
  .container .right-box .breadcrum-nav{
    padding-bottom:10px;
    background: #f1f1f1;
    padding:10px;
    margin-bottom:10px;
  }
   .container .right-box .page-box{
    padding:10px;
   }
   .pagination-box{
    float: right;
    margin-top:20px;
    margin-bottom: 20px;
  }
</style>



