<template>
	<div>
		<div>
			<el-form :inline="true" :model="search" class="demo-form-inline">
			  <el-form-item label="关键字">
			    <el-input v-model="search.key" placeholder="用户名或手机"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button type="primary" @click="onSearch">查询</el-button>
			  </el-form-item>
			  <el-form-item style="float:right">
			  	<input style="display: none;" ref="myinput" type="file" accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" @change="getFile">
				  <el-button @click="exportData" style="margin-right:10px;">导出</el-button>
				  <el-button @click="insertData" style="margin-right:10px;">导入</el-button>
			  	<el-button @click="onAdd">添加用户</el-button>
			  </el-form-item>
			</el-form>
			<el-table
			ref="multipleTable"
		    :data="tableData"
		    border
		    style="width: 100%">
		    <el-table-column
		      prop="user_id"
		      label="用户ID"
		      width="80">
		    </el-table-column>
		    <el-table-column
		      prop="username"
		      label="昵称">
		    </el-table-column>
		    <el-table-column 
		      prop="mobile"  
		      label="手机号">
		    </el-table-column>	
		    <el-table-column
		      prop="payment"
		      label="付费用户">
		      <template slot-scope="scope">{{scope.row.payment?'是':'否'}}</template>
		    </el-table-column>    
			<el-table-column
		      label="操作">
		      <template slot-scope="scope">
		      	<el-button
		          @click="deleteRow(scope.row)"
		          type="text"
		          size="small">
		          删除
		        </el-button>
		      </template>
		    </el-table-column>
		</el-table>
			<div class="pagelist">
				<el-pagination
				  background
				  layout="prev, pager, next,total"
				  @current-change="onPageChange"
				  :current-page.sync="page.current"
				  :page-size="page.pageSize"
				  :total="page.total">
				</el-pagination>
			</div>
			
			<el-dialog :title="dialogTitle" v-if="showDialog" :visible.sync="showDialog">
			  <user-list @onSelectedChange="onSelectedChange"></user-list>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="showDialog = false">取 消</el-button>
			    <el-button type="primary" @click="onSelectedOk">保 存</el-button>
			  </div>
			
			</el-dialog>
			 
			
		</div>
	</div>
</template>
<script>
import Service from '@/service/index'
import CommonJs from '@/utils/common'
import UserList from '@/components/common/SelectUserList'
	export default{
		data(){
			return{
				search:{
					key:''
				},
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        },
		        dialogTitle:'新增课程',
		        showDialog:false,
		        textbooks:[],
		        imgDialog:false,
		        courseId:'',
		        selectedUserList:[]
			}
		},
		watch: {
		    
		},
		mounted(){
			this.courseId=this.$route.params.courseId;
			this.requestData();
		},
		methods:{
			//导入学生
			insertData(){
				this.$refs.myinput.click();
			},
			//导出学生
			exportData(){
				let token=this.$store.state.token;
				if(token && token.split(' ').length>1){
					token=token.split(' ')[1];
				}
				window.open(`${process.env.API_URL}/api/adm/course/${this.courseId}/users?type=export&key=${this.search.key}&token=${token}`)
			},
			getFile(e){
				const data = new FormData();
                data.append('file',e.target.files[0])
                data.append('course_id',this.courseId)

                Service.importExcelToUser(data).then(res=>{
					if(res.data.result=='success'){
                       this.page.current=1;
					   this.requestData();
                       this.$message.success('导入成功!');
                    }else{
					    this.page.current=1;
						this.requestData();
					    let template = `<strong>${res.data.message}</strong>`;
					    res.data.data.forEach(v=>{
					        let temp = `<p>用户：${v.mobile}</p><p>错误信息：${v.err}</p>`
							template+=temp;
						});
                        this.$notify({
                            title: '导入结果',
							type: 'warning',
                            duration: 0,
                            dangerouslyUseHTMLString: true,
                            message: template
                        });
                        // this.$message.error(res.data.message);
                    }
				})

			},
			onClassPageChange(current){
				this.classPage.current=current;
				let start=(current-1)*this.classPage.pageSize,
					end=current*this.classPage.pageSize;

				this.classListFilter=this.course.class_list.slice(start,end);
			},
			onShowTimepicker(scope){
		        this.courseTime={
		        	current_calass_id:scope.row.class_id,
		        	date:scope.row.start_at,
		        	index:scope.row.number-1
		        };
		        this.course.class_list[scope.row.number-1].show_picker=true;
			},
			onDateBlur(number){
				this.course.class_list[number-1].show_picker=false;
				this.courseTime.index=number-1;
			},
			onChangeDate(val){
				let _date=new Date(val.replace(/-/g,'/'));
				this.course.class_list.forEach((item,idx)=>{
					if(idx>this.courseTime.index){
						_date.setDate(_date.getDate()+1);
						item.start_at=CommonJs.dateFmt(_date,"yyyy-MM-dd");
					}
					
				})
				
			},
			onSearch(){
				this.requestData();
			},
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize,
	    			course_id:this.courseId,
	    			key:this.search.key
	    		}
				Service.courseUserList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			//删除用户
			deleteRow(row){
				this.$confirm('确认把该用户从该课程中移除吗?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          Service.courseDeleteUser(this.courseId,row.user_id).then(res=>{
		          	if(res.data.result=='success'){
		          		this.page.current=1;
		          		this.requestData();
		          		this.$message.success(res.data.message);
		          	}else{
		          		this.$message.error(res.data.message);
		          	}
		          })
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消删除'
		          });          
		        });
			},
			onAdd(){
		        this.dialogTitle='添加用户';
				this.showDialog=true;
			},
			
			//当选择项改变时
			onSelectedChange(selectedList){
				this.selectedUserList=selectedList;
			},
			onSelectedOk(){
				if(this.selectedUserList.length>0){
					let ids=[];
					this.selectedUserList.forEach(item=>{
						ids.push(item.id);
					});

					let json={
						courseId:this.courseId,
						userIds:ids
					}
					Service.courseAddUser(json).then(res=>{
						if(res.data.result=='success'){
							this.page.current=1;
							this.requestData();
							this.$message.success(res.data.message);
							this.showDialog=false;
						}else{
							this.$message.error(res.data.message);
						}
					})
				}else{
					this.$message({
			          message: '请至少选择一个成员加入',
			          type: 'warning'
			        });
				}
				
			}

		},
		components:{
			UserList
		}
		
	}
</script>
<style scope>
	.pic-box{
		display: inline-block;
		width:200px;
		height:150px;
		border:1px solid #e0e0e0;
		overflow: hidden;
	}
	.pic-box img{
		width:100%;
	}
	.up-btn{
		vertical-align: text-bottom;
	    margin-left: 10px;
	    margin-bottom: 5px;
	}
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
	.my-datepicker{
		position:absolute;
	}
</style>


