<template>
	<el-dialog title="上传图片" :close-on-click-modal="false" :visible.sync="visible" class="img-cropper-box" @close="closeModal">
	  <el-row>
	    <el-col>
	      <el-button type="primary" @click="onUpload">选择图片</el-button>
	      <span class="img-title">{{cropper.title}}</span>
	      <input type="file" class="hidden" id="upfile" @change="doUploadC"/>
	    </el-col>
	  </el-row>
	  <div class="cropper-box">
	  	<div class="canvas-box"><img id="image"  ></div>
	  	<div class="show-cropper-box"></div>
	  </div>
	  <div slot="footer" class="dialog-footer">
	    <el-button @click="closeModal">取 消</el-button>
	    <el-button type="primary" @click="onSave" :loading="loading">剪切并上传</el-button>
	  </div>
	</el-dialog>
</template>
<script>
import Uploader from '@/service/upload'
	export default{
		props:{
			visible:Boolean,
			params:Object,
			ratio:Number//传值为：none  则不限制裁切比例
		},
		data(){
			return{
				cropper:{
					title:'',
					upload_url:''
				},
				loading:false
			}
		},
		mounted(){
			let ratio=this.ratio?this.ratio:16/9;
			 setTimeout(() => {
			 	$('#image').cropper({
				  aspectRatio: ratio,
				  preview: '.show-cropper-box',
				  crop: function(e) {
				    // Output the result data for cropping image.
				  }
				});
			 },0);
	        $(document.body).on('keydown', function (e) {
	        	let $image=$("#image");
		      switch (e.which) {
		        case 37:
		          e.preventDefault();
		          $image.cropper('move', -1, 0);
		          break;

		        case 38:
		          e.preventDefault();
		          $image.cropper('move', 0, -1);
		          break;

		        case 39:
		          e.preventDefault();
		          $image.cropper('move', 1, 0);
		          break;

		        case 40:
		          e.preventDefault();
		          $image.cropper('move', 0, 1);
		          break;
		      }

		    });

		},
		methods:{
			closeModal(){
				this.$emit("closeModal");
			},
			onUpload(){
		      $('#upfile').trigger('click')
		    },
		    requestUpLoad(file){
		    	let fileName=(new Date().getTime())+'.png';
		    	let path=this.params.uploadType+'/'+fileName;
		    	Uploader.upload(file,path, (response, baseUrl) => {
	               let url =response.url; //baseUrl + response.key;
	               this.$emit("onUploaded",url,this.params);
	               this.loading=false;
	               this.$message.success("图片上传成功");
	            });
		    },
		    doUploadC(evt){
		        let files = evt.target.files;
		        if (files.length === 0) {
		          this.$message.warning("请选择一张图片上传");
		          return;
		        }
		        let file = files[0];
		        if (!file.type.match('image.*')) {
		          this.$message.warning("文件格式不正确");
		          return;
		        }
		         if (file.size > 0 && file.size > 5242880) {
		          this.$message.warning("文件不能超过5M");
		          return;
		        }
		        let URL = window.URL || window.webkitURL, 
		            blobURL;
		        blobURL = URL.createObjectURL(file);
		        this.cropper.title=file.name;
		        this.cropper.upload_url=blobURL;
		        //this.cropper.pic_url=blobURL;
		        $("#image").prop("src",blobURL)
		        $('#image').one('built.cropper', function () {
		          URL.revokeObjectURL(blobURL); // Revoke when load complete
		        }).cropper('reset', true).cropper('replace', blobURL);
		        $("#upfile").val('');
		    },
		    onSave(){
		    	if(this.cropper.upload_url){
		    		this.loading=true;
			    	let crop_url=$("#image").cropper("getCroppedCanvas").toDataURL('image/png');
			    	let _blob=this.dataURLtoBlob(crop_url)
			    	this.requestUpLoad(_blob);
		    	}else{
		    		this.$message.info("请选择一张图片上传");
		    	}
		    	
		    },
		    dataURLtoBlob(dataurl) {
			    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
			        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
			    while(n--){
			        u8arr[n] = bstr.charCodeAt(n);
			    }
			    return new Blob([u8arr], {type:mime});
			}
		}
	}
</script>
<style>
.img-cropper-box .img-title {
  padding-left: 10px; }

.img-cropper-box .cropper-box {
  height: 300px;
  margin-top: 10px;
  position: relative; }
  .img-cropper-box .cropper-box .canvas-box {
    margin-right: 180px;
    height: 100%;
    overflow: hidden;
    border: 1px solid #dedede; }
    .img-cropper-box .cropper-box .canvas-box img, .img-cropper-box .cropper-box .canvas-box canvas {
      max-height: 100%;
      width: auto; }
  .img-cropper-box .cropper-box .show-cropper-box {
    position: absolute;
    right: 0;
    top: 0;
    width: 160px;
    height: 90px;
    border: 1px solid #dedede;
    overflow: hidden; }
    .hidden {
  display: none;
  visibility: hidden; }

</style>	



