import Vue from 'vue'
import Router from 'vue-router'
import store from "@/store";
import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/Login'

import TeachingManage from '@/views/TeachingManage'
import TextBook from '@/components/TextBook'
import TextBookMonth from '@/components/TextBook_Month'
import TextBookWeek from '@/components/TextBook_Week'
import TextBookDay from '@/components/TextBook_Day'
import TextBookClass from '@/components/TextBook_Class'

import CourseManager from '@/components/CourseManager'
import CourseUsers from '@/components/CourseUserList'
import ClassList from '@/components/ClassList'
import ClassUserList from '@/components/ClassUserList'

import UserManage from '@/views/UserManage'
import UserList from '@/components/UserList'

import OrderManage from '@/views/OrderManage'
import OrderList from '@/components/OrderList'

import TeacherManage from '@/views/TeacherManage'
import TeacherList from '@/components/TeacherList'




Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect:'/teachingmanage/textbook'
    },
    {
      path: '/index',
      name: 'HelloWorld',
      component: HelloWorld,
      meta:{
        title:'首页'
      }
    },
    {
      path: '/teachingmanage',
      name: 'TeachingManage',
      component: TeachingManage,
      redirect:'/teachingmanage/textbook',
      meta:{
        title:'教研管理'
      },
      children:[
        {
          path: '/teachingmanage/textbook',
          name: 'TextBook',
          component: TextBook,
          meta:{
            title:'教材管理'
          },
          children:[
            {
              path: '/teachingmanage/textbook/:bookId',
              name: 'TextBookMonth',
              component: TextBookMonth,
              meta:{
                title:'月主题编辑'
              },
              children:[
                {
                  path: '/teachingmanage/textbook/:bookId/:monthId',
                  name: 'TextBookWeek',
                  component: TextBookWeek,
                  meta:{
                    title:'周主题编辑'
                  },
                  children:[
                    {
                      path: '/teachingmanage/textbook/:bookId/:monthId/:weekId',
                      name: 'TextBookDay',
                      component: TextBookDay,
                      meta:{
                        title:'日主题编辑'
                      },
                      children:[
                        {
                          path: '/teachingmanage/textbook/:bookId/:monthId/:weekId/:dayId',
                          name: 'TextBookClass',
                          component: TextBookClass,
                          meta:{
                            title:'课时编辑'
                          },
                        },
                      ]
                    }
                  ]
                }
              ]
            }
          ]
        },
        {
          path: '/teachingmanage/course',
          name: 'course',
          component: CourseManager,
          meta:{
            title:'课程管理'
          },
          children:[
            {
              path: '/teachingmanage/course/:courseId/users',
              name: 'CourseUsers',
              component: CourseUsers,
              meta:{
                title:'课程学员'
              },
            },
              {
                  path:'/teachingmanage/course/:courseId',
                  name:'ClassList',
                  component:ClassList,
                  meta:{
                      title:'课程班级列表'
                  },
                  children:[
                      {
                          path:'/teachingmanage/course/:courseId/:classId',
                          name:'ClassUserList',
                          component:ClassUserList,
                          meta:{
                              title:'班级用户列表'
                          }
                      }
                  ]
              }
          ]
        }
      ]
    },
    {
      path:'/usermanage',
      name:'UserManage',
      component:UserManage,
      redirect:'/usermanage/list',
      meta:{
        title:'用户管理'
      },
      children:[
        {
          path:'/usermanage/list',
          name:'UserList',
          component:UserList,
          meta:{
            title:'用户列表'
          }
        }
      ]
    },
    {
      path:'/ordermanage',
      name:'OrderManage',
      component:OrderManage,
      redirect:'/ordermanage/list',
      meta:{
        title:'订单管理'
      },
      children:[
        {
          path:'/ordermanage/list',
          name:'UserList',
          component:OrderList,
          meta:{
            title:'订单列表'
          }
        }
      ]
    },
    {
      path:'/teachermanage',
      name:'TeacherManage',
      component:TeacherManage,
      redirect:'/teachermanage/list',
      meta:{
        title:'教师管理'
      },
      children:[
        {
          path:'/teachermanage/list',
          name:'TeacherList',
          component:TeacherList,
          meta:{
            title:'教师列表'
          }
        }
      ]
    },
    {
    	path:'/login',
    	name:'login',
    	component:Login,
      meta:{
        skip_auth:true,
        layout:false
      }
    },

  ]
});

router.beforeEach((to,from,next)=>{
  //在已登录状态下不允许退回登录页面去
  if(to.name=='login' && store.state.token){
    next(false);
    return;
  }
  //设置面包屑导航信息
  let bc=[], _queryParams=to.params;
  to.matched.forEach((item)=>{
    let _fullPath='',
       pArr=item.path.match(item.regex);
      if(pArr.length>1){
        _fullPath=pArr[0]
        for(let i=1;i<pArr.length;i++){
          _fullPath=_fullPath.replace(pArr[i],_queryParams[pArr[i].replace(':','')]);
        }
      }else{
        _fullPath=item.path;
      }
    if(item.meta.title){
      bc.push({
        name:item.meta.title,
        path:_fullPath
      });
    }
  });
  store.dispatch('setBreadcrumb',bc);
  store.dispatch('setLayout',to.meta.layout);
  store.dispatch('setSelectedMenu',to.path);

  //拦截需要登录但是未登录的页面请求
  if (to.matched.some(record => record.meta.skip_auth !== true) && !store.state.token) {
    next({
      path: '/login'
    })
  } else {
    next()
  }

})

export default router;



