<template>
	<div>
		<div v-if="!showDetail">
            <el-button @click="onAdd" style="margin-bottom:10px;float:right">新增学员</el-button>
			<el-table
					:data="tableData"
					border
					style="width: 100%">
				<el-table-column
						prop="user_id"
						label="用户ID"
				>
				</el-table-column>
				<el-table-column
						prop="nickname"
						label="微信昵称"
				>
				</el-table-column>
				<el-table-column
						prop="mobile"
						label="手机"
				>
				</el-table-column>
				<el-table-column
						prop="user_status"
						label="用户状态"
				>
				</el-table-column>
				<el-table-column
						prop="course_num"
						label="浏览课包数"
				>
				</el-table-column>
				<el-table-column
						prop="course_all_num"
						label="浏览课包次数"
				>
				</el-table-column>
				<el-table-column
						prop="is_view_course"
						label="是否可以看课程"
				>
				</el-table-column>
				<el-table-column
						prop="last_login_at"
						label="最后登录时间"
				>
				</el-table-column>
				<el-table-column
						prop="is_buy_course"
						label="是否购课"
				>
				</el-table-column>

				<el-table-column
						label="操作"
						width="120">
					<template slot-scope="scope">
						<el-button
								@click="editRow(scope.row)"
								type="text"
								size="small">
							编辑
						</el-button>
						<el-button
								@click="deleteRow(scope.row.id)"
								type="text"
								size="small">
							移出
						</el-button>
					</template>
				</el-table-column>

			</el-table>
		</div>
		<div class="pagelist">
			<el-pagination
					background
					layout="prev, pager, next,total"
					@current-change="onPageChange"
					:current-page.sync="page.current"
					:page-size="page.pageSize"
					:total="page.total">
			</el-pagination>
		</div>
		<el-dialog :title="dialogTitle" v-if="showDialog" :visible.sync="showDialog">
			<el-form ref="saveuser"  :model="user" label-width="120px">
                <el-form-item v-if="!user.id" label="手机号">
                    <el-input v-model="user.mobile" auto-complete="off"></el-input>
                </el-form-item>
				<el-form-item v-if="user.id" label="是否可以看课" prop="is_view_course">
					<el-radio v-model="user.is_view_course" label="1">是</el-radio>
					<el-radio v-model="user.is_view_course" label="0">否</el-radio>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="showDialog = false">取 消</el-button>
				<el-button type="primary" @click="onSave">保 存</el-button>
			</div>
		</el-dialog>
	</div>

</template>
<script>
    import Service from '@/service/index'
    export default{
        data(){
            return{
                showDetail:false,
                class_id:this.$route.params.classId,
                course_id:this.$route.params.courseId,
                tableData: [],
                showDialog:false,
                page:{
                    current:1,
                    pageSize:20,
                    total:0
                },
                user:{
                    id:'',
                    is_view_course:'',
                    mobile:'',
                },
            }
        },
        watch: {
            '$route' (to, from) {
                if(to.name=='ClassUserList'){
                    this.showDetail=false;
                    this.requestData();
                }else{
                    this.showDetail=true;
                }
            }
        },
        mounted(){
            this.requestData();
        },
        methods:{
            requestData(){
                let data={
                    page:this.page.current,
                    per_page:this.page.pageSize,
					class_id:this.class_id
                }
                Service.classUserList(data).then(res=>{
                    if(res.data.result=='success'){
                    this.tableData=res.data.data.data;
                    this.page.current=res.data.data.current_page;
                    this.page.total=res.data.data.total;
                }else{
                    this.$message.error(res.data.message);
                }
            })
            },
            onPageChange(current){
                this.page.current=current;
                this.requestData();
            },
            onAdd(){
                this.dialogTitle='新增学员';
                this.user={
                    id:'',
                    is_view_course:'',
                }
                this.showDialog=true;

            },
            //点击查询
            onSearch(){
                //调用查询接口
                this.requestData();
            },
            editRow(row){
                this.user={
                    id:row.id,
                    is_view_course:row.is_view_course == '是'?'1':'0',
                }

                this.dialogTitle='编辑用户';
                this.showDialog=true;
            },
            //删除
            deleteRow(id){
                this.$confirm('确认删除吗？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Service.deleteClassUser(id).then(res=>{
                    if(res.data.result=="success"){
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.page.current=1;
                    this.requestData();
                }else{
                    this.$message({
                        type: 'error',
                        message: res.data.message
                    });
                }
            })

            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
            },
            onSave(){
                let json={
                    is_view_course:this.user.is_view_course,
                    mobile:this.user.mobile,
                    course_id:this.course_id,
                    class_id:this.class_id,
					id:this.user.id
                };

                Service.saveClassUser(json).then(res=>{
                    if(res.data.result=='success'){
                        this.$message.success(res.data.message);
                        this.showDialog=false;
                        this.page.current=1;
                        this.requestData();
                    }else{
                        this.$message.warning(res.data.message);
                    }
                })

            },

        }
    }
</script>
<style scope>

	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


