import Vue from "vue";
let uploadURL = 'http://up.qiniu.com';
let imageBase = 'http://cdn.singsingenglish.com/';
export default {

  upload: function (file, fileName, callback, progressFn) {
    let token = '', tokenUrl = 'static/ueditor/php/controller.php?action=getToken';//'http://106.14.62.138:81/api/qiniu/uptoken';
    
    let xhr_Token = new XMLHttpRequest();
    xhr_Token.open('POST', tokenUrl, true);
    let formData_Token;
    formData_Token = new FormData();
    formData_Token.append('key', fileName);
    xhr_Token.onreadystatechange = function (response) {
    if (xhr_Token.readyState == 4 && xhr_Token.status == 200 && xhr_Token.responseText != "") {
      //callback(JSON.parse(xhr.responseText), imageBase)
      let resJson=JSON.parse(xhr_Token.responseText);
      let xhr = new XMLHttpRequest();
      xhr.open('POST', uploadURL, true);
      let formData;
      formData = new FormData();
      formData.append('token', resJson.token);
      formData.append('key', fileName);
      formData.append('file', file);
      //上传进度回调
      xhr.upload.addEventListener("progress", function (evt) {
        if (evt.lengthComputable) {
          let percentComplete = Math.round(evt.loaded * 100 / evt.total);
          if (progressFn) {
            progressFn(percentComplete);
          }
        }
      });
      xhr.onreadystatechange = function (response) {
        if (xhr.readyState == 4 && xhr.status == 200 && xhr.responseText != "") {
          callback(JSON.parse(xhr.responseText), imageBase)
        } else if (xhr.status != 200 && xhr.responseText) {
          callback(xhr.responseText)
        }
      };
      xhr.send(formData);

    } else if (xhr_Token.status != 200 && xhr_Token.responseText) {
      callback(xhr_Token.responseText)
    }
  };
  xhr_Token.send(formData_Token);



    // Vue.http.post(tokenUrl,{key:fileName}).then((response) => {
    //   console.log(response);
    //   //imageBase=response.data.data.url+'/';
    //   let xhr = new XMLHttpRequest();
    //   xhr.open('POST', uploadURL, true);
    //   let formData;
    //   formData = new FormData();
    //   formData.append('token', response.data.token);
    //   formData.append('key', fileName);
    //   formData.append('file', file);
    //   //上传进度回调
    //   xhr.upload.addEventListener("progress", function (evt) {
    //     if (evt.lengthComputable) {
    //       let percentComplete = Math.round(evt.loaded * 100 / evt.total);
    //       if (progressFn) {
    //         progressFn(percentComplete);
    //       }
    //     }
    //   });
    //   xhr.onreadystatechange = function (response) {
    //     if (xhr.readyState == 4 && xhr.status == 200 && xhr.responseText != "") {
    //       callback(JSON.parse(xhr.responseText), imageBase)
    //     } else if (xhr.status != 200 && xhr.responseText) {
    //       callback(xhr.responseText)
    //     }
    //   };
    //   xhr.send(formData);
    // })
  }
}




