<template>
	<div>
		<el-form :inline="true" :model="search" class="demo-form-inline">
		  <el-form-item label="关键字">
		    <el-input v-model="search.key" placeholder="用户名或手机"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSearch">查询</el-button>
		  </el-form-item>
		</el-form>
		
		<el-table
		    :data="tableData"
		    border
		    ref="multipleTable"
		    row-key="id"
		    @selection-change="handleSelectionChange"
		    style="width: 100%">
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      prop="username"
		      label="昵称">
		    </el-table-column>
		    <el-table-column 
		      prop="mobile"  
		      label="手机号">
		    </el-table-column>
		    <el-table-column 
		      label="微信登录"
		      >
		      <template slot-scope="scope">
		      	{{scope.row.wechat?'已登录':'未登录'}}
		      </template>
		    </el-table-column>
		</el-table>
		<div class="pagelist">
			<el-pagination
			  background
			  layout="prev, pager, next,total"
			  @current-change="onPageChange"
			  :current-page.sync="page.current"
			  :page-size="page.pageSize"
			  :total="page.total">
			</el-pagination>
		</div>
	</div>
</template>
<script>
import Service from '@/service/index'
	export default{
		data(){
			return{
				search:{
					key:''
				},
				showDetail:false,
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:10,
		        	total:0
		        },
		        selected:[],
			}
		},
		
		mounted(){
			//this.requestData();
		},
		filters:{
			payMentFilter(val){
				return val?'已付款':'未付款'
			}
		},
		methods:{
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize,
	    			key:this.search.key
	    		}
				Service.userList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
						if(this.selected[this.page.current-1]){
							this.tableData.forEach((row, index) => {
					        	let _idx=this.selected[this.page.current-1].findIndex(sel=>sel.id==row.id);
					        	if(_idx>-1){
					        	  this.$nextTick(() => {
					              	this.$refs.multipleTable.toggleRowSelection(row, true);
					              })
					        	}
					        })
						}
						
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			//点击查询
			onSearch(){
				//调用查询接口
				this.requestData();
			},
			handleSelectionChange(val){
				this.selected[this.page.current-1]=val;
				let myArray = this.selected.filter(function(n) { return n; });
				this.$emit('onSelectedChange',[].concat.apply([],myArray));
			}

		}
	}
</script>
<style scope>
	
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


