<template>
	<div>
		<el-form :inline="true" :model="search" class="demo-form-inline">
		  <el-form-item label="关键字">
		    <el-input v-model="search.key" placeholder="用户名或手机"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSearch">查询</el-button>
		  </el-form-item>
		  <el-form-item style="float:right">
		  	<el-button @click="onAdd">新增用户</el-button>
		  	<el-button @click="onExport">导出</el-button>
		  </el-form-item>
		</el-form>
		
		<el-table
		    :data="tableData"
		    border
		    style="width: 100%">
		    <el-table-column
		      prop="id"
		      label="用户ID"
		      width="80">
		    </el-table-column>
		    <el-table-column
		      prop="username"
		      label="昵称">
		    </el-table-column>
			<el-table-column
					prop="birthday"
					label="生日">
			</el-table-column>
			<el-table-column
					prop="level"
					label="等级">
			</el-table-column>
		    <el-table-column 
		      prop="mobile"  
		      label="手机号">
		    </el-table-column>
		    <el-table-column 
		      label="微信登录"
		      >
		      <template slot-scope="scope">
		      	{{scope.row.wechat?'已登录':'未登录'}}
		      </template>
		    </el-table-column>
		    <el-table-column 
		      prop="mobile"  
		      label="付费用户">
		       <template slot-scope="scope">
		      	{{scope.row.payment?'是':'否'}}
		      </template>
		    </el-table-column>
			<el-table-column
		      label="操作">
		      <template slot-scope="scope">
		      	<el-button
		          @click="editRow(scope.row)"
		          type="text"
		          size="small">
		          编辑
		        </el-button>
				  <el-button
						  @click="deleteRow(scope.row.id)"
						  type="text"
						  size="small">
					  删除
				  </el-button>
		      </template>
		    </el-table-column>
		</el-table>
		<div class="pagelist">
			<el-pagination
			  background
			  layout="prev, pager, next,total"
			  @current-change="onPageChange"
			  :current-page.sync="page.current"
			  :page-size="page.pageSize"
			  :total="page.total">
			</el-pagination>
		</div>
	
		<el-dialog :title="dialogTitle" v-if="showDialog" :visible.sync="showDialog">
		  <el-form ref="saveuser" :rules="rules" :model="user" label-width="70px">
		    <el-form-item label="昵称">
		      <el-input v-model="user.username" auto-complete="off"></el-input>
		    </el-form-item>
			  <el-form-item v-if="user.id" label="生日">
				  <el-date-picker
						  v-model="user.birthday"
						  type="date"
						  value-format="yyyy-MM-dd"
						  placeholder="选择日期">
				  </el-date-picker>
			  </el-form-item>
		    <el-form-item label="手机号" prop="mobile">
		      <el-input v-model="user.mobile" auto-complete="off"></el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="showDialog = false">取 消</el-button>
		    <el-button type="primary" @click="onSave">保 存</el-button>
		  </div>
		</el-dialog>
	</div>
</template>
<script>
import Service from '@/service/index'
	export default{
		data(){
			return{
				search:{
					key:''
				},
				showDetail:false,
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        },
		        dialogTitle:'',
		        showDialog:false,
		        user:{
		        	id:'',
		        	username:'',
		        	mobile:'',
					birthday:'',
		        },
		         rules: {
		          mobile: [
		            {required: false, validator:this.checkMobile ,trigger: 'blur'},
		          ],
		      	}

			}
		},
		
		mounted(){
			this.requestData();
		},
		filters:{
			payMentFilter(val){
				return val?'已付款':'未付款'
			}
		},
		methods:{
			checkMobile(rule,value,callback){
		        var regExp=/^1[3|7|5|8][0-9]\d{4,8}$/;
		        if (!value) {
		          callback();
		        }else if(!regExp.test(value)){
		          callback(new Error("手机号码格式错误"));
		        } else {
		           callback();
		        }
		      },
		      //导出用户
		    onExport(){
		    	let token=this.$store.state.token;
		    	if(token && token.split(' ').length>1){
		    		token=token.split(' ')[1];
		    	}
		    	window.open(`${process.env.API_URL}/api/adm/users?type=export&key=${this.search.key}&token=${token}`)
		    },
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize,
	    			key:this.search.key
	    		}
				Service.userList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			onSave(){
				 this.$refs['saveuser'].validate((valid) => {
				 	if(valid){
				 		let json={
							username:this.user.username,
							mobile:this.user.mobile,
							birthday:this.user.birthday
						};
						if(this.user.id){
							json.id=this.user.id;
						}
						Service.saveUser(json).then(res=>{
							if(res.data.result=='success'){
								this.$message.success(res.data.message);
								this.showDialog=false;
								this.page.current=1;
								this.requestData();
							}else{
								this.$message.warning(res.data.message);
							}
						})
				 	}
				 	
				 });
				
			},
			onAdd(){
				this.user={
		        	id:'',
		        	username:'',
		        	mobile:''
		        },
				this.dialogTitle='新增用户';
				this.showDialog=true;
			},
			editRow(row){
				this.user={
					id:row.id,
		        	username:row.username,
		        	mobile:row.mobile,
					birthday:row.birthday
				}
				this.dialogTitle='编辑用户';
				this.showDialog=true;
			},
            //删除
            deleteRow(id){
                this.$confirm('确认删除吗？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Service.deleteUsers(id).then(res=>{
                        if(res.data.result=="success"){
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.page.current=1;
                            this.requestData();
                        }else{
                            this.$message({
                                type: 'error',
                                message: res.data.message
                            });
                        }
                    })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
			
			//点击查询
			onSearch(){
				//调用查询接口
				this.requestData();
			}

		}
	}
</script>
<style scope>
	
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
</style>


