<template>
	<div class="wrap">
		<div class="wrap-main">
			<h1>登陆</h1>
			<el-form :model="login" :rules="loginRules" ref="loginForm">
			  <el-form-item prop="username">
			    <el-input v-model="login.username" placeholder="用户名"></el-input>
			  </el-form-item>
			  <el-form-item prop="password">
			    <el-input type="password" v-model="login.password" placeholder="密码"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button class="btn" size="medium" type="primary" @click="submitForm">登陆</el-button>
			  </el-form-item>
			</el-form>
		</div>
		
	</div>
</template>
<script>
import service from '@/service/index'
	export default{
		data(){
			return{
				login:{
					username:'',
					password:''
				},
				loginRules:{
					username:[
						{ required: true, message: '请输入您的用户名', trigger: 'blur' }
					],
					password:[
						{ required: true, message: '请输入您的密码', trigger: 'blur' }
					]


				}
			}
			
		},
		mounted(){

		},
		methods:{
			submitForm(){
				this.$refs["loginForm"].validate((valid) => {
		          if (valid) {
		            service.login({"username":this.login.username,"password":this.login.password}).then((res)=>{
		            	if(res.data.result=="success"){
		            		if(res.data.data.user.role=='student'){
		            			this.$message.warning('您没有权限登录此系统');
		            			return;
		            		}
		            		this.$store.dispatch('setToken',res.data.data.access_token);
		            		this.$store.dispatch('setUserName',res.data.data.user.username);
		            		//this.$store.dispatch('setIdentity',res.data.data.user.role);
		            		this.$router.push('/teachingmanage/textbook');
		            	}else{
		            		this.$message.error(res.data.message);
		            	}
		            	
		            });
		          } else {
		            this.$message.error('用户名或密码错误，请检查');
		            return false;
		          }
		        });
			}
		}

	}
</script>
<style scoped>
.wrap{
	position: absolute;
	left:0;
	right:0;
	top:0;
	bottom:0;
	overflow: hidden;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;

}
.wrap-main{
	width:300px;
	text-align: center;
}
.wrap-main h1{
	font-size: 22px;
}
.btn{
	width:100%;
}
</style>


