<template>
	<div>
		<div v-if="!showDetail">
			<el-button @click="onAdd" style="margin-bottom:10px;float:right">新增课程</el-button>
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      prop="id"
			      label="编号"
			      width="80">
			    </el-table-column>
			    <el-table-column
			      prop="title"
			      label="课程名称"
			      width="180">
					<template slot-scope="scope">
						<router-link :to="{name:'ClassList', params: { courseId: scope.row.id }}">{{scope.row.title}}</router-link>
					</template>
			    </el-table-column>
			    <el-table-column
			      prop="instructor"
			      label="老师"
			      width="180">
			    </el-table-column>
			    <el-table-column
			      label="适应年龄"
			      width="180">
			      <template slot-scope="scope">
			      	{{scope.row.adapt_age_min}}-{{scope.row.adapt_age_max}}
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="textbook_name"
			      label="对应教材">
			    </el-table-column>
			    <el-table-column
			      prop="month_count"
			      label="当前状态">
			      <template slot-scope="scope">
			      	{{scope.row.status=="publish"?'已上架':'未上架'}}
			      </template>
			    </el-table-column>
				<el-table-column
			      label="操作"
			      width="120">
			      <template slot-scope="scope">
			      	<el-button
			          @click="editRow(scope.row)"
			          type="text"
			          size="small">
			          编辑
			        </el-button>
			        <el-button
			          @click="go2Users(scope.row.id)"
			          type="text"
			          size="small">
			          课程学员
			        </el-button>
			      </template>
			    </el-table-column>
			</el-table>
			<div class="pagelist">
				<el-pagination
				  background
				  layout="prev, pager, next,total"
				  @current-change="onPageChange"
				  :current-page.sync="page.current"
				  :page-size="page.pageSize"
				  :total="page.total">
				</el-pagination>
			</div>
			
			<el-dialog :title="dialogTitle" :visible.sync="showDialog">
			  <el-form :model="course" label-width="90px">
			    <el-form-item label="课程名称">
			      <el-input v-model="course.title" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="课程类型">
			    	<el-select v-model="course.type">
			    		<el-option value="try" label="试用"></el-option>
			    		<el-option value="formal" label="常规"></el-option>
			    	</el-select>
			    	
			    </el-form-item>
			    <el-form-item label="价格">
			      <el-input v-model="course.price" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="老师">
			      <el-input v-model="course.instructor" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item label="老师二维码">
			      <div>
			      	<div class="pic-box"><img v-if="course.teacherWechatCodeUrl" :src="course.teacherWechatCodeUrl" alt=""></div>
			      	<el-button @click="onShowCropper" class="up-btn">选择图片</el-button>
			      </div>
			    </el-form-item>
				<el-form-item label="开始日期">
					<el-date-picker
				      v-model="course.start_at"
				      type="date"
				      value-format="yyyy-MM-dd"
				      placeholder="选择日期">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="结束日期">
					<el-date-picker
				      v-model="course.end_at"
				      type="date"
				      value-format="yyyy-MM-dd"
				      placeholder="选择日期">
				    </el-date-picker>
				</el-form-item>
			    <el-form-item label="选择教材">
			       <el-select @change="onTextbookChange" v-model="course.textbook_id" placeholder="请选择">
				    <el-option
				      v-for="item in textbooks"
				      :key="item.id"
				      :label="item.title"
				      :value="item.id">
				    </el-option>
				  </el-select>
			    </el-form-item>
			    <el-form-item label="上课日期">
			       <el-table
				    :data="classListFilter"
				    border
				    style="width: 100%">
				    <el-table-column
				      prop="number"
				      label="课时"
				      width="50"
				      >
				    </el-table-column>
				    <el-table-column
				      prop="title"
				      label="名称"
				      >
				    </el-table-column>
				    <el-table-column
				      label="对应日期">
				      <template slot-scope="scope">
				      	<p v-if="!scope.row.show_picker" @click="onShowTimepicker(scope)">{{scope.row.start_at}}</p>
				      	<el-date-picker
						  v-if="scope.row.show_picker"
					      v-model="scope.row.start_at"
					      value-format="yyyy-MM-dd"
					      type="date"
					      @change="onChangeDate"
					      @blur="onDateBlur(scope.row.number)"
					      placeholder="选择日期">
				    	</el-date-picker>
				      </template>
				    </el-table-column>
				  </el-table>
				  	<div class="pagelist">
						<el-pagination
						  background
						  layout="prev, pager, next,total"
						  @current-change="onClassPageChange"
						  :current-page.sync="classPage.current"
						  :page-size="classPage.pageSize"
						  :total="classPage.total">
						</el-pagination>
					</div>
			    </el-form-item>
			    <el-form-item label="是否上架">
			      <el-checkbox v-model="course.is_publish">上架</el-checkbox>
			    </el-form-item>

			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="showDialog = false">取 消</el-button>
			    <el-button type="primary" @click="onSave">保 存</el-button>
			  </div>
			</el-dialog>
			<cropper :ratio="1/1" v-if="imgDialog" :params="{uploadType:'course'}" :visible="imgDialog" @closeModal="closeCropper" @onUploaded="onUploaded"></cropper>
		</div>
		<router-view v-else></router-view>
	</div>
</template>
<script>
import Service from '@/service/index'
import CommonJs from '@/utils/common'
import cropper from '@/components/common/cropper.vue'
	export default{
		data(){
			return{
				tableData: [],
		        page:{
		        	current:1,
		        	pageSize:20,
		        	total:0
		        },
		        showDetail:false,
		        dialogTitle:'新增课程',
		        showDialog:false,
		        textbooks:[],
		        imgDialog:false,
		        //课程实体
		        course:{
		        	id:'',
		        	title:'',
		        	textbook_id:'',
		        	class_list:[],
		        	is_publish:false,
		        	price:'',
		        	instructor:'',
		        	teacherWechatCodeUrl:'',
		        	type:'',
		        	start_at:'',
		        	end_at:''
		        },
		        //修改课时时间使用
		        courseTime:{
		        	current_calass_id:'',
		        	date:'',
		        	index:''
		        },
		        //课时列表数据
		        classListFilter:[],
		        classPage:{
		        	current:1,
		        	pageSize:10,
		        	total:0
		        },
		        imgDialog:false
		        

			}
		},
		watch: {
		    '$route' (to, from) {
		    	if(to.name=='course'){
		    		this.showDetail=false;
		    		this.requestData();
		    	}else{
		    		this.showDetail=true;
		    	}
		     }
		},
		mounted(){
			if(this.$route.params.courseId){
				this.showDetail=true;
			}else{
				this.showDetail=false;
			}
			Service.textBookList({page:1,per_page:999}).then(res=>{
				if(res.data.result=='success'){
					this.textbooks=res.data.data.data;
					
				}else{
					this.$message.error(res.data.message);
				}
			})
			this.requestData();
		},
		methods:{
			//当选择的教材变动时
			onTextbookChange(val){
				Service.getClassesByTextBookId(val).then(res=>{
					if(res.data.result=='success'){
						let _data=[];
						res.data.data.forEach((item,idx)=>{
							let _isShow=false;
							if(!item.start_at && idx==0){
								_isShow=true;
							}
							_data.push({
								textbook_calendar_id:item.id,
								start_at:'',
								title:item.title,
								show_picker:_isShow,
								number:idx+1
							});
						});
						this.course.class_list=_data;
						this.requestClassFilterData(1);

						this.courseTime={
				        	current_calass_id:'',
				        	date:'',
				        	index:''
				        }	
					}else{
						this.$message.error(res.data.message);
					}
					
				})
			},
			requestClassFilterData(current){
				this.classPage.current=current;
				this.classPage.total=this.course.class_list.length;
				let start=(current-1)*this.classPage.pageSize,
					end=current*this.classPage.pageSize;

				this.classListFilter=this.course.class_list.slice(start,end);
				
			},
			onClassPageChange(current){
				this.classPage.current=current;
				let start=(current-1)*this.classPage.pageSize,
					end=current*this.classPage.pageSize;

				this.classListFilter=this.course.class_list.slice(start,end);
			},
			onShowTimepicker(scope){
				//this.datePickerPosition.left=event.target.offsetLeft//event.x;
		        //this.datePickerPosition.top=event.target.offsetTop//event.y;
		        this.courseTime={
		        	current_calass_id:scope.row.class_id,
		        	date:scope.row.start_at,
		        	index:scope.row.number-1
		        };
		        this.course.class_list[scope.row.number-1].show_picker=true;
		        // let _idx=this.course.class_list.findIndex(item=>item.class_id==row.class_id);

		        // if(_idx>-1){
		        // 	this.course.class_list[_idx].show_picker=true;
		        // }

			},
			onDateBlur(number){
				this.course.class_list[number-1].show_picker=false;
				this.courseTime.index=number-1;
			},
			onChangeDate(val){
				let _date=new Date(val.replace(/-/g,'/'));
				this.course.class_list.forEach((item,idx)=>{
					if(idx>this.courseTime.index){
						_date.setDate(_date.getDate()+1);
						item.start_at=CommonJs.dateFmt(_date,"yyyy-MM-dd");
					}
					
				})

				// if(this.courseTime.current_calass_id){
				// 	let _idx=this.course.class_list.findIndex(item=>item.class_id==this.courseTime.current_calass_id);
					
			 //        if(_idx>-1){
			 //        	//this.course.class_list[_idx].show_picker=false;

			 //        	this.course.class_list.forEach((item,idx)=>{
				// 			if(idx>_idx){
				// 				_date.setDate(_date.getDate()+1);
				// 				item.start_at=CommonJs.dateFmt(_date,"yyyy-MM-dd");
				// 			}
						
				// 		})
			 //        }
				// }else{
				// 	//没值，默认是第一个设置
				// 	//this.course.class_list[0].show_picker=false;
					
				// 	this.course.class_list.forEach((item,idx)=>{
				// 		if(idx>0){
				// 			_date.setDate(_date.getDate()+1);
				// 			item.start_at=CommonJs.dateFmt(_date,"yyyy-MM-dd");
				// 		}
						
				// 	})
				// }
			},
			requestData(){
				let data={
	    			page:this.page.current,
	    			per_page:this.page.pageSize
	    		}
				Service.courseList(data).then(res=>{
					if(res.data.result=='success'){
						this.tableData=res.data.data.data;
						this.page.current=res.data.data.current_page;
						this.page.total=res.data.data.total;
					}else{
						this.$message.error(res.data.message);
					}
				})
			},
			onPageChange(current){
				this.page.current=current;
				this.requestData();
			},
			onSave(){
				let json={
					title:this.course.title,
					textbook_id:this.course.textbook_id,
					status:this.course.is_publish?'publish':'draft',
					price:this.course.price,
					date_list:this.course.class_list,
					instructor:this.course.instructor,
					instructor_qr:this.course.teacherWechatCodeUrl,
					type:this.course.type,
					start_at:this.course.start_at,
					end_at:this.course.end_at

				};
				if(this.course.id){
					json.id=this.course.id;
				}
				Service.saveCourse(json).then(res=>{
					if(res.data.result=='success'){
						this.$message.success(res.data.message);
						this.showDialog=false;
						this.page.current=1;
						this.requestData();
					}else{
						this.$message.warning(res.data.message);
					}
				})
			},
			onAdd(){
				this.course={
		        	id:'',
		        	title:'',
		        	textbook_id:'',
		        	class_list:[],
		        	is_publish:false,
		        	price:'',
		        	instructor:'',
		        	teacherWechatCodeUrl:'',
		        	type:'',
		        	start_at:'',
		        	end_at:''

		        },
		        this.classListFilter=[];
		        this.classPage.current=1;
		        this.classPage.total=0;
		        this.dialogTitle='新增课程';
				this.showDialog=true;
			},
			editRow(row){
				Service.courseDetail(row.id).then(res=>{
					if(res.data.result=="success"){
						let _classList=[];
						res.data.data.date_list.forEach((c,idx)=>{
							_classList.push({
								textbook_calendar_id:c.textbook_calendar_id,
								start_at:c.start_at.split(' ')[0],
								title:c.title,
								show_picker:false,
								number:idx+1
							})
						})
						this.course={
							id:row.id,
				        	title:res.data.data.title,
				        	price:res.data.data.price,
				        	textbook_id:res.data.data.textbook_id,
				        	is_publish:res.data.data.status=="publish",
				        	class_list:_classList,
				        	instructor:res.data.data.instructor,
				        	teacherWechatCodeUrl:res.data.data.instructor_qr,
				        	type:res.data.data.type,
				        	start_at:res.data.data.start_at,
				        	end_at:res.data.data.end_at
						}
						this.requestClassFilterData(1);
						this.dialogTitle='编辑课程';
						this.showDialog=true;
					}
				})
				
			},
			go2Users(courseId){
				this.$router.push('/teachingmanage/course/'+courseId+'/users')
			},
			onShowCropper(){
		        this.imgDialog=true;
		    },
			closeCropper(){
				this.imgDialog=false;
			},
			onUploaded(url){
				this.course.teacherWechatCodeUrl=url;
				this.imgDialog=false;
			},

		},
		components:{
			cropper
		}
		
	}
</script>
<style scope>
	.pic-box{
		display: inline-block;
		width:200px;
		height:200px;
		border:1px solid #e0e0e0;
		overflow: hidden;
	}
	.pic-box img{
		width:100%;
	}
	.up-btn{
		vertical-align: text-bottom;
	    margin-left: 10px;
	    margin-bottom: 5px;
	}
	.pagelist{
		margin-top:10px;
		text-align: right;
	}
	.my-datepicker{
		position:absolute;
	}
</style>


